import React, { FC } from "react";

import Label from "../Label/Label";
import { spacingMap } from "../styleguide";
import uuid from "../utils/uuid";

import { InnerToggle, StyledToggleButton, ToggleWrapper } from "./Toggle.styles";
import { ToggleProps } from "./Toggle.types";

const Toggle: FC<ToggleProps> = ({
	dataQA,
	isActive = false,
	disabled,
	onClick,
	id = uuid(),
	className,
	label,
	m,
	ml,
	mr,
	mt,
	mb,
	type = "button",
	...props
}) => {
	// Pass down the reverse active state to flip the switch, and the event
	const modifiedOnChange = (event: React.MouseEvent<HTMLButtonElement>) => onClick?.(event, !isActive);

	return (
		<ToggleWrapper
			m={m}
			ml={ml}
			mr={mr}
			mt={mt}
			mb={mb}
		>
			<StyledToggleButton
				role="switch"
				isActive={isActive}
				data-qa={dataQA}
				className={className}
				aria-label={label}
				id={id}
				aria-checked={isActive}
				disabled={disabled}
				onClick={(event) => modifiedOnChange(event)}
				type={type}
				{...props}
			>
				<InnerToggle isActive={isActive} disabled={disabled} aria-hidden />
			</StyledToggleButton>
			{label && <Label htmlFor={id} dataQA={`${dataQA}-label`} ml={spacingMap.xs}>{label}</Label>}
		</ToggleWrapper>
	);
};

Toggle.displayName = "Toggle";
export default Toggle;
